'use server'

import Link from "next/link";

async function ActionListItem({ action }) {

    // Name and Description should be defined for every action.
    const nameData = await fetch("http://localhost:3001/metadata?key=Name&actionId=" + action.id, {
        method: "GET",
        headers: {
            "Content-Type": "application/json"
        }
    });
    const name = (await nameData.json())[0];
    console.log("name", name);
    const descriptionData = await fetch("http://localhost:3001/metadata?key=Description&actionId=" + action.id, {
        method: "GET",
        headers: {
            "Content-Type": "application/json"
        }
    });
    const description = (await descriptionData.json())[0];
    console.log("description", description);

    return (
        <Link
            href={"/dashboard/action/" + action.id}
        >
            <div className="flow flow-col gap-2 border border-solid border-black rounded-md p-2">
                <h2 className="text-lg font-bold">{"Name: " + name.value}</h2>
                <p>{"Description: " + description.value}</p>
                <p>{"Type: " + action.action_type}</p>
                <p>{"Frozen: " + action.is_frozen}</p>
            </div>
        </Link>
    )
}

export default async function ActionList({ searchParams, className }) {
    let queryParams = "";
    if (searchParams) {
        // use a "?" for the first query param
        queryParams += "?"
        let firstParam = true;

        Object.keys(searchParams).forEach(key => {
            // use an "&" for subsequent query params
            if (!firstParam) {
                queryParams += "&"
            } else {
                firstParam = false;
            }

            const value = searchParams[key];
            queryParams += `${key}=${value}`;
        });
    }

    const data = await fetch("http://localhost:3001/actions" + queryParams, {
        method: "GET",
        headers: {
            "Content-Type": "application/json"
        }
    });

    let actions = [];
    if (data.ok) {
        actions = await data.json();
    } else {
        // Optionally log the error response text for debugging
        const errorText = await data.text();
        console.error("Failed to fetch actions:", data.status, errorText);
        // You can also show a user-friendly error message or return an empty list
    }

    return (
        <div className="flex flex-col gap-2">
            {actions.map((action) => (
                <ActionListItem key={action.id} action={action} />
            ))}
        </div>
    )
}